.app__story {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 16px;
    line-height: 1.28em;
    @media (min-width: $tablet){
        padding: 16px 32px;
    }
    @media (min-width: $desktop){
        padding: 16px 48px;
    }
}